import { ComponentChildren } from 'preact';
import { MutableRef, useCallback } from 'preact/hooks';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { Floating } from '../UNSAFE_Floating';
import { Layer } from '../UNSAFE_Layer';
import useOutsideClick from '../hooks/UNSAFE_useOutsideClick';
import { isControlOrFunctionKey, KEYS } from './eventUtils';

const rootStyles = {
  base: cssProps`${{
    boxSizing: 'border-box',
    backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-popup-bg-color)',
    border: '1px solid var(--oj-c-PRIVATE-DO-NOT-USE-popup-border-color)',
    borderRadius: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-border-radius-md)',
    boxShadow: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-dropdown-box-shadow)',

    display: 'flex',
    flexDirection: 'column'
  }}`
};

type Props = {
  anchorRef: MutableRef<HTMLElement | null>;
  children?: ComponentChildren;
  dropdownRef: MutableRef<HTMLElement | null>;
  id?: string;
  isOpen?: boolean;
  onAutoDismiss?: (event?: Event) => void;
};

export function Dropdown({ anchorRef, children, dropdownRef, id, isOpen, onAutoDismiss }: Props) {
  const handleAutoDismiss = useCallback(
    (event: Event) => {
      onAutoDismiss?.(event);
    },
    [onAutoDismiss]
  );

  const handleKeyDown = useCallback(
    (event: KeyboardEvent) => {
      if (event.defaultPrevented || isControlOrFunctionKey(event)) {
        return;
      }

      switch (event.code) {
        case KEYS.ESC:
        case KEYS.TAB:
          handleAutoDismiss(event);
          break;
      }
    },
    [handleAutoDismiss]
  );

  useOutsideClick({
    isDisabled: !isOpen,
    ref: [anchorRef, dropdownRef],
    handler: handleAutoDismiss
  });

  // TODO: get dropdown y (mainAxis) offset from --oj-c-PRIVATE-DO-NOT-USE-private-core-global-dropdown-offset
  const offsetValue = { mainAxis: 4, crossAxis: 0 };

  const inlineStyle = {
    minWidth: `${anchorRef.current?.offsetWidth}px`
  };

  return !isOpen ? null : (
    <Layer>
      <Floating
        anchorRef={anchorRef}
        ref={dropdownRef}
        placement="bottom-start"
        offsetValue={offsetValue}>
        <div class={rootStyles.base} id={id} style={inlineStyle} onKeyDown={handleKeyDown}>
          {children}
        </div>
      </Floating>
    </Layer>
  );
}
