import { ComponentProps } from 'preact';
import { useCallback } from 'preact/hooks';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { DataState, ListItemContext } from '../UNSAFE_Collection';
import { HighlightText } from '../UNSAFE_HighlightText';
import { ListView } from '../UNSAFE_ListView';
import { ItemTextType, renderItemText } from './itemTextUtils';

const itemStyles = cssProps`${{
  height: '20px',
  paddingBottom: '0.75rem',
  paddingLeft: '0.75rem',
  paddingRight: '0.75rem',
  paddingTop: '0.75rem'
}}`;

type PickedPropsFromListView = Pick<
  ComponentProps<typeof ListView>,
  'accessibleSummary' | 'onCurrentKeyChange' | 'onSelectionChange' | 'viewportConfig'
>;

type Props<K extends string | number, D extends Record<string, any>> = PickedPropsFromListView & {
  itemText: ItemTextType<K, D>;
  // props from ListView that we constrain or unconstrain
  currentKey?: K;
  data?: DataState<K, D> | null;
  onLoadRange?: ComponentProps<typeof ListView>['onLoadRange'];
  searchText?: string;
  selectedKeys?: Set<K>;
};

const noOp = () => {};

export function DefaultList<K extends string | number, D extends Record<string, any>>({
  accessibleSummary,
  currentKey,
  data,
  itemText,
  onCurrentKeyChange,
  onLoadRange = noOp,
  onSelectionChange,
  searchText,
  selectedKeys
}: Props<K, D>) {
  const listItemRenderer = useCallback(
    (listItemContext: ListItemContext<K, D>) => {
      const itemContext = {
        data: listItemContext.data,
        key: listItemContext.metadata.key,
        metadata: listItemContext.metadata
      };
      const renderedItemText = renderItemText(itemContext, itemText) as string;
      return (
        <div class={itemStyles}>
          <HighlightText matchText={searchText}>{renderedItemText}</HighlightText>
        </div>
      );
    },
    [itemText, searchText]
  );

  return (
    <ListView<K, D>
      accessibleSummary={accessibleSummary}
      currentKey={currentKey}
      data={data ?? null}
      gridlines="hidden"
      onCurrentKeyChange={onCurrentKeyChange}
      onLoadRange={onLoadRange}
      onSelectionChange={onSelectionChange}
      selectedKeys={{ all: false, keys: selectedKeys ?? new Set<K>() }}
      selectionMode="multiple">
      {(context: ListItemContext<K, D>) => listItemRenderer(context)}
    </ListView>
  );
}
