import { ComponentChildren } from 'preact';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { classNames } from '../utils/UNSAFE_classNames';

const rootStyles = {
  base: cssProps`${{
    paddingLeft: '0',
    paddingRight: '0',
    margin: '1px 0 2px 0',
    position: 'relative',
    overflowX: 'hidden',
    overflowY: 'auto',
    // '-webkit-tap-highlight-color': 'transparent',
    maxHeight: '400px',
    boxSizing: 'border-box',

    display: 'flex',
    flexDirection: 'column'
  }}`,

  // TODO: This should really be applied directly to the child ListView so that the content
  // displays under the padding when scrolled, but we can't apply styles to the child collection.
  // TODO: Also need to figure out how to determine whether the collection is a ListView (in
  // the collectionRenderer case) in order to apply this styling.
  listView: cssProps`${{
    paddingTop: '.5rem',
    paddingBottom: '.5rem'
  }}`,

  loading: cssProps`${{
    minHeight: '10rem'
  }}`
};

type Props = {
  children?: ComponentChildren;
  isLoading?: boolean;
};

export function DropdownList({ children, isLoading = false }: Props) {
  const styleClasses = classNames([
    rootStyles.base,
    rootStyles.listView,
    isLoading && rootStyles.loading
  ]);
  return <div class={styleClasses}>{children}</div>;
}
